  padding: 3px 9px 4px;
  margin-left: 12px;
}

.notification-popover .card-title {
  font-size: 20px;
  line-height: 28px;
  color: #2E2E2E;
  padding-top: 16px;
  padding-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.notification-popover .MuiCardContent-root ul .dropdown-item {
  align-items: center;
  display: flex;
  padding: 20px 15px;
  border-bottom: 1px solid #EFF0F7;
}

.notification-popover .dropdown-item p {
  color: #2E2E2E;
  font-size: 14px;
  line-height: 18px;
  padding-bottom: 0;
}

.notification-popover .MuiCardContent-root ul .dropdown-item.active {
  background-color: #EFF0F7;
}

.notification-popover .MuiCardContent-root ul .dropdown-item:hover {
  background-color: #F6F6FB;
}

.notification-popover .card-footer .MuiLink-root {
  color: #F08557;
  padding: 10px;
  text-transform: none;
  text-align: center;
  cursor: pointer;
  border-radius: 4px;
  display: block;
}

.notification-popover .MuiCardContent-root:last-child {
  padding-bottom: 0;
}

.notification-popover .card-footer .MuiLink-root:hover {
  background-color: #f6f6f6;
  text-decoration: none;
}

.notification-popover .dropdown-item .list-icon {
  padding: 4px;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 30px;
  background-color: #EBEBEB;
  border-radius: 50%;
  margin-right: 14px;
}

.notification-popover .dropdown-item.active .list-icon {
  background-color: #F08557;
}

.notification-popover .dropdown-item .list-icon img {
  display: block;
}


/* MainNavigation
--------------------------------------------------------------------------------------------------------*/
.main-navigation {
  background-color: #fff;
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  height: 100%;
  overflow: auto;
  z-index: 5;
}

.main-navigation .logo {
  display: block;
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;
  text-align: center;
  width: 92px;
  margin: 36px auto 70px;
  display: block;
}

.main-navigation .MuiAccordion-root {
  box-shadow: none;
  border: 0;
  margin: 0 0 4px !important;
}

.main-navigation .MuiAccordion-root::before {
  display: none;
}

.main-navigation .MuiAccordionSummary-root {
  min-height: auto !important;
  padding: 6px 20px;
}

.main-navigation .MuiAccordionSummary-root.has-submenu .MuiAccordionSummary-content {
  width: calc(100% - 12px);
}

.main-navigation .MuiAccordionSummary-root.has-submenu .MuiAccordionSummary-expandIconWrapper {
  min-width: 12px;
}

.main-navigation .MuiAccordionDetails-root {
  padding: 6px 20px;
}

.main-navigation .MuiAccordionSummary-content {
  margin: 0 !important;
  width: 100%;
}

.main-navigation .menu-link {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 18px;
  color: #000;
  width: 100%;
}

.main-navigation .menu-link:hover,
.main-navigation .menu-link:focus {
  color: #F08557;
}

.main-navigation .MuiAccordionSummary-root .menu-link span {
  width: calc(100% - 35px);
  display: inline-block;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.main-navigation .menu-link .thumb-i {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  min-width: 35px;
  height: 35px;
  position: relative;
  background-color: #F5F5F5;
  border-radius: 4px;
  margin-right: 5px;
}

.main-navigation .menu-link .thumb-i img.overlap {
  display: none;
}

.main-navigation .menu-link.active .thumb-i {
  background-color: #FFF4E8;
}

.main-navigation .menu-link.active .thumb-i img {
  display: none;
}

.main-navigation .menu-link.active .thumb-i img.overlap {
  display: block;
}

.main-navigation .MuiAccordion-root:not(.not-expanded) .MuiAccordionSummary-root.Mui-expanded .menu-link .thumb-i {
  background-color: #FFF4E8;
}

.main-navigation .MuiAccordion-root:not(.not-expanded) .MuiAccordionSummary-root.Mui-expanded .menu-link .thumb-i img {
  display: none;
}

.main-navigation .MuiAccordion-root:not(.not-expanded) .MuiAccordionSummary-root.Mui-expanded .menu-link .thumb-i img.overlap {
  display: block;
}

.main-navigation .sub-menu {
  margin-left: 36px;
}

.main-navigation .sub-menu .menu-link {
  font-size: 12px;
  line-height: 16px;
  padding: 6.5px 16px 6.5px 7px;
  position: relative;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: block;
}

.main-navigation .sub-menu .menu-link:hover,
.main-navigation .sub-menu .menu-link.active {
  color: #F08557;
}

.main-navigation .sub-menu .menu-link::before {
  content: "";
  position: absolute;
  top: 11px;
  right: 1px;
  width: 8px;
  height: 8px;
  background-position: center;
  background-size: 8px;
  background-image: url(./assets/images/right-arrow.svg);
  background-repeat: no-repeat;
}

/* toggle-menu */
.mobile-bg-shadow {
  position: fixed;
  top: 64px;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 4;
  visibility: hidden;
  opacity: 0;
}


.toggle-menu .main-navigation {
  left: 0;
}

.toggle-menu .mobile-bg-shadow {
  visibility: visible;
  opacity: 1;
}


/* Main Content
--------------------------------------------------------------------------------------------------------*/
.wrapper {
  padding: 86px 0 0 250px;
  min-height: 100vh;
}

.main-content {
  padding: 0px 25px 25px;
}

.main-card {
  background-color: #fff;
  padding: 25px;
  min-height: calc(100vh - 86px - 25px);
}

.inner-card {
  background-color: #F5F5F5;
  padding: 25px;
